.templateCard {
    width: 45%;
    height: 10%;
    margin: 10px 0 10px 0;
    border-radius: 20px;
    box-shadow: 0px 1px 6px -4px rgba(0,0,0,0.48) inset;
    -webkit-box-shadow: 0px 1px 6px -4px rgba(0,0,0,0.48) inset;
    -moz-box-shadow: 0px 1px 6px -4px rgba(0,0,0,0.48) inset;
    border: 2px solid #0000002a;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    font-family: "Roboto Condensed", sans-serif;
    -ms-flex-align: center;
        align-items: center;
    overflow: hidden;
    min-height: 356px;
}

.templateCard__title {
    position: relative;
    width: 100%;
    height: 70%;
    display: grid;
    place-items: center;
    z-index: 500;
    cursor: pointer;
}

.templateCard__title h2 {
    margin: 0;
    padding: 0;
    font-size: 4rem;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    transition: all ease .3s;
}

.templateCard__title p {
    display: none;
}

.templateCard__preview {
    position: absolute;
    display: grid;
    place-items: center;
    opacity: 0;
    width: 100%;
    height: 100%;
    font-size: 3rem;
    margin-top: 10px;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    transition: all ease .3s;
}

.templateCard__title:hover > .templateCard__preview {
    margin-top: 0;
    opacity: 1;
}

.templateCard__title:hover > .templateCard__title h2 {
    margin-bottom: 10px;
}

.templateCard__descContainer {
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    height: 30%;
    -ms-flex-align: center;
        align-items: center;
    background-color: #ffffff;
    z-index: 550;
}

.templateCard__desc {
    width: 70%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    -ms-flex-align: center;
        align-items: center;
}

.templateCard__info {
    width: 80%;
    height: 100%;
    font-size: 1.2rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-pack: center;
        justify-content: center;
}

.templateCard__download {
    width: 20%;
    height: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    -ms-flex-align: center;
        align-items: center;
    font-size: 2rem;
    margin: 0;
    padding: 0;
}

.templateCard__downloadDesc {
    position: absolute;
    display: grid;
    place-items: center;
    opacity: 0;
    width: 100%;
    height: 100%;
    font-size: 3rem;
    margin-top: 10px;
    -webkit-transition: all ease .3s;
    -o-transition: all ease .3s;
    transition: all ease .3s;
}

.templateCard__downloadDesc--view {
    opacity: 1;
    margin-top: 0;
}

.templateCard__downloadIcon {
    color: #00000079;
    cursor: pointer;
    font-size: 3rem !important;
    margin: 0;
    padding: 0;
    -webkit-transition: color ease .3s;
    -o-transition: color ease .3s;
    transition: color ease .3s;
}

.templateCard__downloadIcon:hover {
    color: #000000;
}

.templateCard__snackbarCreateError {
    background-color: #310d20af !important;
    font-family: "Roboto Condensed", sans-serif !important;
    font-size: 1.3rem !important;
    color: #ffffff !important;
}

@media (max-width: 800px) {
    .templateCard {
        width: 80%;
        height: 1%;
    }
    
    .templateCard__title {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: center;
            justify-content: center;
    }
    
    .templateCard__title h2 {
        font-size: 2rem;
        margin: 0;
    }
    
    .templateCard__title:hover > .templateCard__preview {
        margin-top: 0;
        opacity: 0;
    }

    .templateCard__downloadDesc--view {
        opacity: 0;
        margin-top: 0;
    }
    
    .templateCard__title p {
        display: block;
    }
}


