.home {
    width: 100%;
    height: 100%;
    background-color: #ffffff;
    position: relative;
    display: -ms-flexbox;
    display: flex;
    overflow: hidden;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.home__decorator {
    background-color: #e4ffadd5;
    bottom: -5%;
    left: -50%;
}

.home__info {
    z-index: 500;
    -ms-flex: .4 1;
        flex: .4 1;
    padding: 40px;
    text-align: left;
    display: grid;
    font-size: 5rem;
    place-items: center;
    font-family: "Roboto Condensed", sans-serif;
}

.home__desc {
    z-index: 500;
    -ms-flex: .4 1;
        flex: .4 1;
    padding: 40px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    height: 80%;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: space-evenly;
        justify-content: space-evenly;
    letter-spacing: 1px;
    font-family: "Roboto Condensed", sans-serif;
}

.home__descParagraph {
    font-size: 1.4rem;
}

.home__links {
    display: -ms-flexbox;
    display: flex;
    width: 90%;
    -ms-flex-pack: distribute;
        justify-content: space-around;
}

.home__linkButton {
    text-decoration: none;
    background-color: #77966D;
    color: #ffffff;
    padding: 10px;
    width: 40%;
    text-align: center;
    border-radius: 100px;
    font-size: 1.2rem;
    letter-spacing: 2px;
    -webkit-transition: all ease .2s;
    -o-transition: all ease .2s;
    transition: all ease .2s;
}

.home__linkButton:hover {
    opacity: .7;
}

@media (max-width: 800px) {
    
    .home {
        -ms-flex-direction: column;
            flex-direction: column;
        overflow-y: scroll;
    }
    
    .home__info {
        font-size: 3rem;
        text-align: center;
        padding: 0;
    }
    
    .home__desc {
        text-align: center;
        padding: 30px;
    }
    
    .home__linkButton {
        font-size: 1.2rem;
        width: 45%;
        margin: 20px;
    }
    
}



